
.terms-modal .weui-dialog {
  width: 100% !important;
  max-width: 345px !important;
  max-height: 80vh;
  border-radius: 0 !important;
}
.terms-modal .weui-mask {
  background: rgba(0, 0, 0, 0.5) !important;
}
.terms-modal .dialog-title {
  padding: 1rem 0;
  color: #484848;
  font-size: 17px;
}
.terms-modal .dialog-content {
  height: 70vh;
  overflow-y: auto;
}
.terms-modal .dialog-text {
  color: #484848;
  margin-top: 1.5rem;
  line-height: 24px;
  font-size: 15px;
  text-align: left;
}
.terms-modal .weui-dialog__bd {
  padding: 1.5rem !important;
}
.terms-modal .weui-dialog__ft {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 1.5rem;
  background-color: #fff;
}
.terms-modal .weui-dialog__btn {
  border-radius: 2px;
  font-size: 17px;
}
.terms-modal .weui-dialog__btn_default {
  display: none;
}
.terms-modal .weui-dialog__btn_primary {
  background-color: #9C8063 !important;
  color: #fff !important;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
}
.terms-modal .weui-dialog__ft:after {
  display: none !important;
}



