
.loader-img {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50;
  width: 100%;
  height: 100vh;
}
.loader-img img {
  width: 100%;
  height: 100vh;
}
.loader-leave-active {
  transition: opacity 0.5s;
}
.loader-leave-to {
  opacity: 0;
}



