
html {
  min-height: 100vh;
  font-size: 10px;
}
@media only screen and (min-width: 376px) {
html {
    font-size: 10px !important;
    max-width: 425px;
    margin: 0 auto;
}
.vux-header,
  .weui-tabbar {
    max-width: 425px;
}
.vux-header,
  .btn-fixed,
  .loader-img {
    left: 50% !important;
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    max-width: 425px;
}
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
div,
p,
a {
  font-size: 15px;
  margin: 0;
}
input,
textarea {
  border: none;
  outline: none;
}
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 425px;
  overflow-x: hidden;
}
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
::-moz-placeholder {
  color: #c0c0c0;
}
::-webkit-input-placeholder {
  color: #c0c0c0;
}
::-ms-input-placeholder {
  color: #c0c0c0;
}
::-webkit-scrollbar {
  display: none;
}
.title {
  text-align: center;
  font-size: 1.6rem;
}
.btn-group {
  margin: 1rem 0;
}
.btn-fixed {
  position: fixed;
  left: 0;
  bottom: 0 !important;
  z-index: 20;
  width: 100%;
  margin: 0;
  max-width: 425px;
}
.btn {
  border: none;
  padding: 1.5rem 3rem;
  text-align: center;
  font-size: 16px;
  box-sizing: border-box;
  text-decoration: none;
  border-radius: 2px;
}
.btn:focus {
  outline: none;
}
.btn-primary {
  color: #fff;
  background-color: #9C8063;
}
.btn-line {
  color: #9C8063;
  border: 1px solid #9C8063;
  background-color: transparent;
}
.btn-block {
  display: block;
  width: 100%;
  border-radius: 0;
}
.btn[disabled] {
  opacity: .5;
}
a.link {
  font-size: 13px;
  color: #9C8063;
  text-decoration: none;
}
.input-group {
  position: relative;
  padding: 1rem 0 .5rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.input-group:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #E5E5E5;
  -webkit-transform: scale(1, 0.5);
  transform: scale(1, 0.5);
}
.input-icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 15px;
          flex: 0 0 15px;
  width: 15px;
  margin-right: 1rem;
  text-align: center;
}
.input-icon img {
  width: 10px;
  margin-top: 12px;
}
.input-text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 1rem 0;
  border: none;
  font-size: 15px;
  outline: none;
  box-sizing: border-box;
}
.text-center {
  text-align: center;
}
.mb-2 {
  margin-bottom: 2rem;
}
.mb-3 {
  margin-bottom: 3rem;
}
.mb-4 {
  margin-bottom: 4rem;
}
.mt {
  margin-top: 1.5rem;
}
.mt-2 {
  margin-top: 2rem;
}
.mt-4 {
  margin-top: 4rem;
}
.split {
  margin: 1.5rem 0;
}
.v--modal {
  margin: 5rem;
  width: auto !important;
  height: auto !important;
  font-size: 1.4rem;
}
.v--modal .modal-head {
  padding: 1rem 0 0 0;
  text-align: center;
  font-size: 1.6rem;
}
.v--modal .modal-body {
  font-size: 1.4rem;
  line-height: 1.7em;
  padding: 1.5rem;
}
.v--modal .modal-footer {
  padding: 1rem;
  text-align: center;
}
.weui-tabbar__label {
  margin: 0 !important;
}
.vux-header {
  position: fixed !important;
  left: 0;
  top: 0;
  z-index: 20;
  width: 100%;
  background-color: #fff !important;
  color: #484848 !important;
}
.vux-header:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #E5E5E5;
  -webkit-transform: scale(1, 0.5);
  transform: scale(1, 0.5);
}
.left-arrow:before {
  top: 7px !important;
  border-top: 2px solid #333 !important;
  border-left: 2px solid #333 !important;
}
.vux-header-left {
  color: #484848 !important;
}
.vux-header-title,
.vux-header h1 {
  font-size: 17px !important;
  color: #484848 !important;
}
.nav-text {
  font-size: 15px;
  color: #848484;
}
.container {
  height: 100%;
  padding: 5.5rem 1.5rem 1.5rem 1.5rem;
}
/*
 *  
 */
.page-enter-active {
  transition: all 0.3s;
}
.page-enter {
  opacity: 0;
}
.page-in-enter-active {
  transition: all 0.3s;
}
.page-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}
.page-out-enter-active {
  transition: all 0.3s;
}
.page-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}
.weui-loadmore {
  margin: 1.5rem auto !important;
}
.weui-loadmore__tips {
  color: #B3B3B3;
}
.avatar {
  position: relative;
}
.avatar .icon-expert {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 17px;
  height: 17px;
  border: 1px solid #fff;
  border-radius: 50%;
  background-color: #FFA400;
}
.avatar .icon-expert:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 17px;
  height: 17px;
  line-height: 17px;
  text-align: center;
  font-size: 11px;
  color: #fff;
  font-style: normal;
}
.empty-tip {
  font-size: 15px;
  color: #C0C0C0;
  text-align: center;
  margin-top: 200px;
}
.empty-tip .icon-warning {
  display: block;
  width: 50px;
  height: 50px;
  margin: 0 auto 2rem auto;
  background-image: url(components/common/img/warning_big@3x.png);
  background-size: 50px 50px;
}
.empty-tip .btn-wrap {
  padding: 20px;
}
.empty-tip .btn-wrap .btn {
  padding: .8rem 2rem;
}
.load-more {
  color: #c0c0c0;
  text-align: center;
}
.lg-preview-wrapper {
  left: 0;
  top: 0;
}
.lg-preview-nav-arrow {
  box-sizing: border-box;
}



